<script>
import { useStore } from "vuex";
import { computed } from "vue";
import UserInfoService from "@/services/userInfo.service";

export default {
    name: 'VerticalNav',
    setup() {
        const store = useStore();
        const sidebarType = computed(() => store.getters["setting/sidebar_type"]);
        const toggleSidebar = () => {
            // Code Here
            if (sidebarType.value.includes("sidebar-mini")) {
                store.dispatch(
                    "setting/sidebar_type",
                    sidebarType.value.filter((item) => item !== "sidebar-mini")
                );
            } else {
                store.dispatch("setting/sidebar_type", [
                    ...sidebarType.value,
                    "sidebar-mini",
                ]);
            }
        };
        return {
            store,
            sidebarType,
            toggleSidebar,
        };
    },
    created() {
    },

    data() {
        return {
            profiles: ['social.profile1', 'social.profile2', 'social.profile3', 'social.profileimage', 'social.profilevideo', 'social.profileevent', 'social.profilebadges', 'social.profileforum'],
            admin: ['book-list', 'admin.statistics', 'admin.accept-users', 'admin.accept-reviewers', 'admin.faq'],
            reviews: ['thesis-review.list', 'general.list', 'questions.list', 'plagiarism-checker', 'ReadyToAudit'],
        }
    },
    methods: {
        checkActive(route) {
            if (this.$route.name === route) {
                return true
            }
            if (route.includes(this.$route.name)) {
                return true
            }
        }
    },
    computed: {
        user() {
            return UserInfoService.getUser();
        },
        isBookTeamMember() {
            return UserInfoService.hasRole(this.user, "book-quality");
        },
        isUserAcceptTeamMember() {
            return UserInfoService.hasRoles(this.user, [
                "user_accept",
            ]);

        },
        isAdmin() {
            return UserInfoService.hasRole(this.user, "admin");
        },
        isSuper() {
            return UserInfoService.hasRoles(this.user, [
                "admin",
                "super_auditer",
                "super_reviewer",
            ]);
        },
        isAuditer() {
            return UserInfoService.hasRoles(this.user, [
                "admin",
                "super_auditer",
                "auditor",
            ]);
        },
        isReviewer() {
            return UserInfoService.hasRoles(this.user, [
                "admin",
                "super_reviewer",
                "reviewer",
            ]);
        },

    },

}
</script>
